<template>
  <div>
    <el-dialog
      v-bind="$attrs"
      v-on="$listeners"
      :close-on-click-modal="false"
      :show-close="false"
    >
    <el-row :gutter="0">
      <el-form
        ref="formRef"
        :model="formData"
        :rules="rules"
        label-width="100px"
        >
        <el-form-item
          label=""
          prop="label"
        >
          <el-input
            v-model="formData.label"
            placeholder=""
            clearable
          />
        </el-form-item>
        <el-form-item
          label=""
          prop="value"
        >
          <el-input
            v-model="formData.value"
            placeholder=""
            clearable
          >
            <el-select
              v-model="defaultType"
              placeholder=""
              slot="append"
              :style="{width: '100px'}"
            >
              <el-option
                v-for="item in dataType"
                :key="item.value"
                :label="item.label"
                :value="item.value"
                :disabled="item.disabled || false"
              />
            </el-select>
          </el-input>
        </el-form-item>
      </el-form>
    </el-row>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="handleConfirm"> </el-button>
        <el-button @click="handleCancel"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'treeNodeDialog',
  data () {
    return {
      formData: {
        label: '',
        value: ''
      },
      rules: {
        label: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        value: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ]
      },
      dataType: [
        { label: '', value: 'string' },
        { label: '', value: 'number' }
      ],
      defaultType: 'string'
    }
  },
  watch: {
    'formData.value' (value) {
      this.defaultType = isNaN(Number(value)) ? 'string' : 'number'
    }
  },
  methods: {
    handleConfirm () {
      this.$refs.formRef.validate(valid => {
        if (!valid) return false
        if (this.defaultType === 'number') {
          this.formData.value = parseFloat(this.formData.value)
        }
        this.$emit('commit', this.formData)
        this.close()
      })
    },
    handleCancel () {
      this.close()
      this.$refs.formRef.resetFields()
    },
    close () {
      this.$emit('update:visible', false)
      // reset data
      this.formData = {
        label: '',
        value: ''
      }
    }
  }
}
</script>
